<script>
import header_wrap from "./header_wrap/header_wrap.vue";
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "result_page",
  data() {
    return {
      // 
      jobname: "Job-12",
      // 
      // 
      Result_Urls: {
        url1: "",
        url2: "",
        url3: "",
        url4: "",
        url5: "",
        url6: "",
        url7: "",
        url8: "",
        url9: "",
        jobname: "",
        inp: "",
      },

      // 
      srcList: [],
    };
  },
  created() {
    this.getResultJobname();
  },
  methods: {
    getResultJobname() {
      // 
      // 
      this.jobname = sessionStorage.getItem("jobname");
      // 
      // this.jobname = this.$route.query.jobname;

      // 
      this.Result_Urls.url1 =
        "http://192.168.31.168:5678/" + this.jobname + "/S-1.png";
      this.Result_Urls.url2 =
        "http://192.168.31.168:5678/" + this.jobname + "/S-2.png";
      this.Result_Urls.url3 =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-1.png";
      this.Result_Urls.url4 =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-2.png";
      this.Result_Urls.url5 =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-1.png";
      this.Result_Urls.url6 =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-2.png";
      this.Result_Urls.url7 =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-1.png";
      this.Result_Urls.url8 =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-2.png";
      this.Result_Urls.url9 =
        "http://192.168.31.168:5678/" + this.jobname + "/Stress-Time chart.png";
      // 
      this.Result_Urls.jobname = this.jobname;

      // inp
      this.Result_Urls.inp =
        "http://192.168.31.168:5678/" + this.jobname + "/Job-2.inp";

      // 
      this.srcList[0] =
        "http://192.168.31.168:5678/" + this.jobname + "/S-1.png";
      this.srcList[1] =
        "http://192.168.31.168:5678/" + this.jobname + "/S-2.png";
      this.srcList[2] =
        "http://192.168.31.168:5678/" + this.jobname + "/Stress-Time chart.png";
      this.srcList[3] =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-1.png";
      this.srcList[4] =
        "http://192.168.31.168:5678/" + this.jobname + "/SR-2.png";
      this.srcList[5] =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-1.png";
      this.srcList[6] =
        "http://192.168.31.168:5678/" + this.jobname + "/U1-2.png";
      this.srcList[7] =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-1.png";
      this.srcList[8] =
        "http://192.168.31.168:5678/" + this.jobname + "/U2-2.png";
    },

    // mongodb
    saveStampingResultFn() {
      // 
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          axios
            .post(API_LIST.saveSimulationResult, this.Result_Urls)
            .then((_d) => {
              if (_d.data.reg_code === 17) {
                this.$message({
                  type: "success",
                  message: _d.data.regInfo,
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });

      // console.log(this.Result_Urls);
    },

    // inp
    downloadResultFn() {
      // inpurl
      var downloadUrl = this.Result_Urls.inp;
      // 
      window.location.href = downloadUrl;
    },
  },
  components: { header_wrap },
};
</script>

