<script>
import header_wrap from "../../main_wrap/header_wrap/header_wrap.vue";
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "result_page",
  data() {
    return {
      avatar: "",
      Simu_success: "",
      // 
      fileList: [],
      // 
      ruleForm: {
        Filename: "",
        partname: "",
        time: "",
        displacement: "",
        jobname: "",
        density1: "",
        density2: "",
        elastic1: "",
        elastic2: "",
        sdp1: "",
        sdp2: "",
        plasticA: "",
        plasticB: "",
        friction: "",
        cellsize2: "",
        cellsize3: "",
        verts: "",
      },
      dialogFormVisible: false,
      importVariable: {
        jobname1: "",
      },
      formLabelWidth: "120px",
    };
  },
  methods: {
    //   
    // 
    submitForm(formName) {
      axios.post(API_LIST.stampVar_post, formName).then((_d) => {
        if (_d.data.reg_code === 13) {
          this.$message({
            message: "!",
            type: "success",
          });
        } else {
          this.$message({
            message: "",
            type: "error",
          });
        }
      });
    },

    // 
    importVariableFn() {
      this.dialogFormVisible = false;
      let importVarObj = {
        jobname: this.importVariable.jobname,
      };
      axios.post(API_LIST.importVar_post, importVarObj).then((_d) => {
        // _d.data
        var arr = _d.data;
        if (arr[0]) {
          let arrObj = {
            Filename: arr[0].Filename,
            partname: arr[0].partname,
            time: arr[0].time,
            displacement: arr[0].displacement,
            jobname: arr[0].jobname,
            density1: arr[0].density1,
            density2: arr[0].density2,
            elastic1: arr[0].elastic1,
            elastic2: arr[0].elastic2,
            sdp1: arr[0].sdp1,
            sdp2: arr[0].sdp2,
            plasticA: arr[0].plasticA,
            plasticB: arr[0].plasticB,
            friction: arr[0].friction,
            verts: arr[0].verts,
            cellsize2: arr[0].cellsize2,
            cellsize3: arr[0].cellsize3,
          };
          this.ruleForm = arrObj;
          this.$message({
            type: "success",
            message: "!",
          });
        } else {
          this.$message({
            type: "error",
            message: "!",
          });
        }
      });
    },

    // 
    start_simulationFn() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          axios.post(API_LIST.startStamp_post, this.ruleForm).then((_d) => {
            // 
            this.Simu_success = _d.data.regInfo;
            this.$alert(this.Simu_success, "Simulation_Platform", {
              confirmButtonText: "",
              callback: () => {
                // 
                sessionStorage.setItem("active_inx", "/result_page");
                this.$router.push({
                  path: "/result_page",
                });
                // 
                sessionStorage.setItem("jobname", this.ruleForm.jobname);
                // 
                location.reload();
              },
            });
          });

          // 
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          location.reload();
          this.$message({
            type: "info",
            message: "!",
          });
        });
    },
    getUploadSuccess(res, file) {
      // this.avatar = res.avatar;
      // console.log(564);
      this.$message({
        type: "success",
        message: "!",
      });
    },
  },
  components: { header_wrap },
};
</script>

