<template>
  <div>
    <div>
      <header_wrap></header_wrap>
    </div>
    <div style="height: 500px" class="stepst">
      <!--  -->
      <el-steps direction="vertical" :active="active">
        <el-step
          title=" 1"
          description=""
        ></el-step>
        <el-step
          title=" 2"
          description=""
        ></el-step>
        <el-step
          title=" 3"
          description=""
        ></el-step
        ><el-step
          title=" 4"
          description=""
        ></el-step>
      </el-steps>

      <!--  -->
      <div class="notice">
        <div style="display: flex; margin-top: 10px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show1" class="transition-box">
              <p>
                {{ show1_text }}
              </p>
            </div>
          </transition>
        </div>
        <div style="display: flex; margin-top: 30px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show2" class="transition-box">
              <p>
                {{ show2_text }}
              </p>
            </div>
          </transition>
        </div>
        <div style="display: flex; margin-top: 30px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show3" class="transition-box">
              <p>
                {{ show3_text }}
              </p>
            </div>
          </transition>
        </div>
        <div style="display: flex; margin-top: 30px; height: 115px">
          <transition name="el-zoom-in-top">
            <div v-show="show4" class="transition-box">
              <p>
                {{ show4_text }}
              </p>
            </div>
          </transition>
        </div>
      </div>

      <!--  -->
      <el-button type="primary" plain @click="next" :disabled="disabled"
        ></el-button
      >
    </div>
  </div>
</template>


<script>
import header_wrap from "./header_wrap/header_wrap.vue";

export default {
  name: "start_page",
  data() {
    return {
      active: 0,
      disabled: false,
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      show1_text:
        " ",
      show2_text:
        "",
      show3_text:
        "inp",
      show4_text:
        "",
    };
  },
  methods: {
    next(active) {
      if (this.active++ == 4) {
        this.active = 0;
      }
      switch (active) {
        case 1:
          this.show1 = true;
          break;
        case 2:
          this.show2 = true;
          break;
        case 3:
          this.show3 = true;
          break;
        case 4:
          this.show4 = true;
          break;
      }

      if (this.active == 1) {
        this.show1 = true;
      }
      if (this.active == 2) {
        this.show2 = true;
      }
      if (this.active == 3) {
        this.show3 = true;
      }
      if (this.active == 4) {
        this.show4 = true;
        this.disabled = true;
      }
    },
  },
  components: { header_wrap },
};
</script>

<style scoped>
.el-steps {
  text-align: left;
  width: 80%;
  margin-left: 100px;
  margin-top: 40px;
  float: left;
}
.stepst {
  position: relative;
}
/*  */
.el-button {
  position: absolute;
  font-size: 20px;
  right: 800px;
  top: 600px;
}
/*  */
.stepst /deep/ .el-step__description {
  font-size: 20px;
  margin-top: 20px;
}
.stepst /deep/.el-step__title {
  font-size: 25px;
}
/*  */
.notice {
  position: absolute;
  top: 10px;
  right: 100px;
}
/*  */
/*  */
.transition-box {
  margin-bottom: 10px;
  width: 400px;
  height: 120px;
  border-radius: 4px;
  background-color: #409eff;
  text-align: center;
  color: #fff;
  padding: 40px 20px;
  box-sizing: border-box;
  margin-right: 20px;
  opacity: 0.8;
}
.transition-box p {
  margin-top: -25px;
  font-family: "";
  text-align: left;
  font-size: 20px;
  text-indent: 30px;
}
</style>
