<script>
import header_wrap from "../../main_wrap/header_wrap/header_wrap.vue";
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  name: "result_page",
  data() {
    return {
      // 
      ruleForm1: {
        jobname: "",
        jobname1: "",
        edges1: "",
        edges2: "",
        edges3: "",
        verts1: "",
        verts2: "",
        verts3: "",
        verts4: "",
        holdtime: 0,
        displacement: 0,
        repoint: 0,
      },
      Simu_success1: "",
      // 
      dialogFormVisible: false,
      importVariable: {
        jobname1: "",
      },
      formLabelWidth: "120px",

      // 
      hold: false,
    };
  },
  methods: {
    // 
    submitForm1(formName) {
      axios.post(API_LIST.springBackVar_post, formName).then((_d) => {
        if (_d.data.reg_code === 14) {
          this.$message({
            message: "!",
            type: "success",
          });
        }
      });
    },

    // 
    importVariableFn() {
      this.dialogFormVisible = false;
      let importVarObj = {
        jobname1: this.importVariable.jobname1,
      };
      axios.post(API_LIST.importBackVar_post, importVarObj).then((_d) => {
        // _d.data
        var arr = _d.data;
        if (arr[0]) {
          // console.log(arr);
          let arrObj = {
            jobname: arr[0].jobname,
            jobname1: arr[0].jobname1,
            edges1: arr[0].edges1,
            edges2: arr[0].edges2,
            edges3: arr[0].edges3,
            verts1: arr[0].verts1,
            verts2: arr[0].verts2,
            verts3: arr[0].verts3,
            verts4: arr[0].verts4,
            holdtime: arr[0].holdtime,
            displacement: arr[0].displacement,
            repoint: arr[0].repoint,
          };
          this.ruleForm1 = arrObj;
          this.$message({
            type: "success",
            message: "!",
          });
        } else {
          // console.log(123);
          this.$message({
            type: "error",
            message: "!",
          });
        }
      });
    },

    // 
    start_simulationFn1() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          // 
          console.log(132);
          axios
            .post(API_LIST.startSpringback_post, this.ruleForm1)
            .then((_d) => {
              console.log(123);
              // 
              this.Simu_success1 = _d.data.regInfo;
              this.$alert(this.Simu_success1, "Simulation_Platform", {
                confirmButtonText: "",
                callback: () => {
                  // 
                  sessionStorage.setItem("active_inx", "/result_page");
                  this.$router.push({
                    path: "/result_page",
                  });
                  // sessionStorage.setItem("jobname", this.ruleForm1.jobname);
                  location.reload();
                },
              });
            });
          // 
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          console.log("!");
          this.$message({
            type: "info",
            message: "!",
          });
        });
    },
  },
  components: { header_wrap },
};
</script>

