<template>
  <div class="table">
    <el-row :gutter="20">
      <el-col :span="16" :offset="2">
        <div class="grid-content bg-purple">
          <!--  -->
          <el-table :data="userInfo" stripe style="width: 100%">
            <el-table-column prop="username" label="" width="180">
            </el-table-column>
            <el-table-column prop="password" label="" width="180">
            </el-table-column>
            <el-table-column prop="email" label="" width="220">
            </el-table-column>
            <el-table-column label="" width="170">
              <template slot-scope="scope">
                <el-button @click="dialogFormVisible = true" size="mini"
                  ></el-button
                >
                <el-button
                  type="danger"
                  size="mini"
                  @click="deleteUserInfoFn(scope.$index, scope.row)"
                  ></el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </div></el-col
      >
    </el-row>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="user">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="user.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="user.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="user.email" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="changeUserInfoFn"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import API_LIST from "@/APIListconfig";

export default {
  data() {
    return {
      // 
      user: {
        username: "",
        password: "",
        email: "",
      },
      // 
      dialogFormVisible: false,
      // 
      formLabelWidth: "80px",
      // 
      userInfo: [],
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    // 
    getUserInfo() {
      axios.get(API_LIST.userInfo).then((_d) => {
        this.userInfo = _d.data;
      });
    },
    // 
    changeUserInfoFn() {
      let _userInfoObj = {
        username: this.user.username,
        password: this.user.password,
        email: this.user.email,
      };
      this.dialogFormVisible = false;

      // 
      axios.post(API_LIST.userInfo_post, _userInfoObj).then((_d) => {
        // 
        this.getUserInfo();
      });
    },

    // 
    deleteUserInfoFn(index, row) {
      var deleteUserInfoObj = this.userInfo[index];
      // 
      axios
        .post(API_LIST.deleteUserInfo_post, deleteUserInfoObj)
        .then((_d) => {});
      // ()
      this.getUserInfo();
    },
  },
};
</script>

<style scoped>
.table {
  margin-top: 30px;
}
/*  */
.table /deep/ .el-dialog {
  width: 400px;
}
.table /deep/ .el-dialog__body {
  padding: 15px 10px;
}
.dialog-footer .el-button {
  margin-top: -50px;
}

/*  */
.el-table /deep/ .cell {
  text-align: center;
}
</style>